import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";

// ─────────────────────────────────────────────────────────────
// 1️⃣ PRESETS
// ─────────────────────────────────────────────────────────────
const MASTERS = ["Driver", "Location"];

const PRESET_FIELDS = {
  Driver: [
    {
      tabName: "General Info",
      sections: [
        {
          sectionName: "Driver Details",
          rows: [
            {
              columnCount: 3,
              fields: [
                { label: "Driver ID", type: "Text", required: true },
                { label: "Full Name", type: "Text", required: true },
                { label: "Status", type: "Select", options: ["Active", "Inactive"] },
              ],
            },
          ],
        },
      ],
    },
  ],
  Location: [
    {
      tabName: "Location Info",
      sections: [
        {
          sectionName: "Address Details",
          rows: [
            {
              columnCount: 2,
              fields: [
                { label: "Location ID", type: "Text", required: true },
                { label: "Location Name", type: "Text", required: true },
              ],
            },
          ],
        },
      ],
    },
  ],
};

// ─────────────────────────────────────────────────────────────
// 2️⃣ COMPONENT
// ─────────────────────────────────────────────────────────────
const ModuleDynamicFormBuilder = () => {
  const [formState, setFormState] = useState({});
  const [activeMaster, setActiveMaster] = useState(MASTERS[0]);
  const [selectedFieldId, setSelectedFieldId] = useState(null);
  const [editorField, setEditorField] = useState(null);

  // ── Init Form State ────────────────────────────────
  useEffect(() => {
    const base = {};
    MASTERS.forEach((m) => {
      const tabs = PRESET_FIELDS[m].map((tab, tabIdx) => ({
        ...tab,
        sections: tab.sections.map((section, secIdx) => ({
          ...section,
          rows: section.rows.map((row, rowIdx) => ({
            ...row,
            fields: row.fields.map((f, idx) => ({
              id: `${m}_${tabIdx}_${secIdx}_${rowIdx}_${idx}`,
              ...f,
            })),
          })),
        })),
      }));
      base[m] = { tabs };
    });
    setFormState(base);
  }, []);

  // ── Helpers ────────────────────────────────────────
  const updateMaster = (callback) => {
    setFormState((prev) => {
      const copy = { ...prev };
      copy[activeMaster] = callback(copy[activeMaster]);
      return copy;
    });
  };

  // ── Add / Delete Items ─────────────────────────────
  const addTab = () => {
    updateMaster((m) => ({
      ...m,
      tabs: [
        ...m.tabs,
        {
          tabName: `New Tab ${m.tabs.length + 1}`,
          sections: [
            {
              sectionName: "New Section",
              rows: [{ columnCount: 2, fields: [] }],
            },
          ],
        },
      ],
    }));
  };

  const addSection = (tabIndex) => {
    updateMaster((m) => {
      const tabs = [...m.tabs];
      tabs[tabIndex].sections.push({
        sectionName: `New Section ${tabs[tabIndex].sections.length + 1}`,
        rows: [{ columnCount: 2, fields: [] }],
      });
      return { ...m, tabs };
    });
  };

  const addRow = (tabIndex, sectionIndex) => {
    updateMaster((m) => {
      const tabs = [...m.tabs];
      tabs[tabIndex].sections[sectionIndex].rows.push({
        columnCount: 2,
        fields: [],
      });
      return { ...m, tabs };
    });
  };

  const addField = (tabIndex, sectionIndex, rowIndex) => {
    updateMaster((m) => {
      const tabs = [...m.tabs];
      const fields = tabs[tabIndex].sections[sectionIndex].rows[rowIndex].fields;
      fields.push({
        id: `${activeMaster}_${tabIndex}_${sectionIndex}_${rowIndex}_${fields.length}`,
        label: "New Field",
        type: "Text",
      });
      return { ...m, tabs };
    });
  };

  const deleteField = (fieldId) => {
    updateMaster((m) => ({
      ...m,
      tabs: m.tabs.map((tab) => ({
        ...tab,
        sections: tab.sections.map((section) => ({
          ...section,
          rows: section.rows.map((row) => ({
            ...row,
            fields: row.fields.filter((f) => f.id !== fieldId),
          })),
        })),
      })),
    }));
    setSelectedFieldId(null);
    setEditorField(null);
  };

  // ── Field Edit Save ────────────────────────────────
  const applyEditorSave = () => {
    if (!editorField) return;
    updateMaster((m) => ({
      ...m,
      tabs: m.tabs.map((tab) => ({
        ...tab,
        sections: tab.sections.map((section) => ({
          ...section,
          rows: section.rows.map((row) => ({
            ...row,
            fields: row.fields.map((f) =>
              f.id === editorField.id ? { ...editorField } : f
            ),
          })),
        })),
      })),
    }));
    setSelectedFieldId(null);
    setEditorField(null);
  };

  // ── UI: Field List ─────────────────────────────────
  const renderFieldsList = () => {
    const tabs = formState[activeMaster]?.tabs || [];

    return tabs.map((tab, tIdx) => (
      <div key={tIdx} className="mb-4 border-b pb-2">
        <div className="flex items-center justify-between">
          <div className="font-semibold text-blue-600">{tab.tabName}</div>
          <Button size="sm" variant="outline" onClick={() => addSection(tIdx)}>
            + Section
          </Button>
        </div>

        {tab.sections.map((section, sIdx) => (
          <div key={sIdx} className="ml-2 mt-2">
            <div className="flex items-center justify-between">
              <div className="font-medium">{section.sectionName}</div>
              <Button
                size="sm"
                variant="outline"
                onClick={() => addRow(tIdx, sIdx)}
              >
                + Row
              </Button>
            </div>

            {section.rows.map((row, rIdx) => (
              <div key={rIdx} className="ml-3 mt-2">
                <div className="flex justify-between mb-1">
                  <div className="text-xs text-gray-500">
                    Row {rIdx + 1} • {row.columnCount} columns
                  </div>
                  <Button
                    size="sm"
                    variant="outline"
                    onClick={() => addField(tIdx, sIdx, rIdx)}
                  >
                    + Field
                  </Button>
                </div>

                <div className="grid grid-cols-1 gap-1">
                  {row.fields.map((f) => (
                    <motion.div
                      key={f.id}
                      whileHover={{ scale: 1.02 }}
                      onClick={() => {
                        setSelectedFieldId(f.id);
                        setEditorField({ ...f });
                      }}
                      className={`cursor-pointer rounded-md border p-2 ${selectedFieldId === f.id
                          ? "border-blue-500 bg-blue-50"
                          : "border-gray-200 hover:bg-gray-50"
                        }`}
                    >
                      <div className="font-semibold">{f.label}</div>
                      <div className="text-xs text-gray-500">{f.type}</div>
                    </motion.div>
                  ))}
                </div>
              </div>
            ))}
          </div>
        ))}
      </div>
    ));
  };

  // ── UI: Editor ─────────────────────────────────────
  const renderEditor = () => {
    if (!editorField)
      return <div className="text-gray-400">Select a field to edit</div>;

    return (
      <div className="space-y-3">
        <div>
          <label className="text-sm font-semibold">Label</label>
          <input
            className="w-full border rounded p-1 text-sm"
            value={editorField.label}
            onChange={(e) =>
              setEditorField((p) => ({ ...p, label: e.target.value }))
            }
          />
        </div>

        <div>
          <label className="text-sm font-semibold">Type</label>
          <select
            className="w-full border rounded p-1 text-sm"
            value={editorField.type}
            onChange={(e) =>
              setEditorField((p) => ({ ...p, type: e.target.value }))
            }
          >
            <option value="Text">Text</option>
            <option value="Number">Number</option>
            <option value="Email">Email</option>
            <option value="Date">Date</option>
            <option value="Select">Select</option>
          </select>
        </div>

        {editorField.type === "Select" && (
          <div>
            <label className="text-sm font-semibold">
              Options (comma-separated)
            </label>
            <input
              className="w-full border rounded p-1 text-sm"
              value={(editorField.options || []).join(", ")}
              onChange={(e) =>
                setEditorField((p) => ({
                  ...p,
                  options: e.target.value.split(",").map((x) => x.trim()),
                }))
              }
            />
          </div>
        )}

        <div>
          <label className="text-sm font-semibold flex items-center gap-2">
            <input
              type="checkbox"
              checked={editorField.required || false}
              onChange={(e) =>
                setEditorField((p) => ({ ...p, required: e.target.checked }))
              }
            />
            Required
          </label>
        </div>

        <div className="flex gap-2">
          <Button size="sm" onClick={applyEditorSave}>
            Save
          </Button>
          <Button
            size="sm"
            variant="outline"
            onClick={() => deleteField(editorField.id)}
          >
            Delete
          </Button>
          <Button
            size="sm"
            variant="ghost"
            onClick={() => {
              setEditorField(null);
              setSelectedFieldId(null);
            }}
          >
            Cancel
          </Button>
        </div>
      </div>
    );
  };

  // ── Render ─────────────────────────────────────────
  return (
    <div className="grid grid-cols-[1fr_2fr_1fr] gap-4">
      {/* Left Panel: Masters */}
      <Card className="p-3 space-y-2">
        <div className="font-semibold text-gray-700 mb-2">Masters</div>
        {MASTERS.map((m) => (
          <Button
            key={m}
            size="sm"
            variant={m === activeMaster ? "default" : "outline"}
            onClick={() => {
              setActiveMaster(m);
              setSelectedFieldId(null);
              setEditorField(null);
            }}
          >
            {m}
          </Button>
        ))}
        <Button
          className="w-full mt-3"
          variant="default"
          size="sm"
          onClick={addTab}
        >
          + Add Tab
        </Button>
      </Card>

      {/* Center Panel: Field List */}
      <Card className="p-3 overflow-auto">{renderFieldsList()}</Card>

      {/* Right Panel: Field Editor */}
      <Card className="p-3">{renderEditor()}</Card>
    </div>
  );
};

export default ModuleDynamicFormBuilder;
